<template>
  <view class="cart-container">
    <!-- 顶部标题 -->
    <u-navbar
      :title="shopType==='food'?'外卖购物车':'商城购物车'"
      :autoBack="true"
      bgColor="#fff"
    />

    <!-- 空车占位 -->
    <view v-if="isEmpty" class="empty-box">
      <u-empty
        mode="car"
        icon="/static/empty-cart.png"
        text="购物车空空如也～"
        marginTop="160rpx"
      >
        <u-button
          type="primary"
          :color="shopType==='food'
            ? 'linear-gradient(90deg,#ff7d00 0%,#ff5700 100%)'
            : 'linear-gradient(90deg,#ff4b7e 0%,#ff3366 100%)'"
          text="去逛逛"
          size="small"
          @click="goHome"
          customStyle="margin-top:48rpx;width:240rpx;border-radius:48rpx"
        />
      </u-empty>
    </view>

    <!-- 商品列表 -->
    <view v-else class="main">
      <!-- 店铺卡片 -->
      <view
        class="store-card"
        v-for="store in cartGroup"
        :key="store.storeId"
      >
        <!-- 店铺标题 -->
        <view class="store-header">
          <u-checkbox
            :value="store.checked"
            shape="circle"
            @change="toggleStore(store)"
          />
          <u-image
            width="40rpx"
            height="40rpx"
            :src="store.storeLogo"
            radius="6rpx"
            margin="0 12rpx"
          />
          <u-text :text="store.storeName" size="28rpx" bold></u-text>
        </view>

        <!-- 商品 -->
        <u-swipe-action>
          <u-swipe-action-item
            v-for="sku in store.list"
            :key="sku.cartId"
            :options="swipeOptions"
            @click="deleteItem(sku)"
          >
            <view class="sku-row">
              <u-checkbox
                :value="sku.checked"
                shape="circle"
                @change="toggleSku(sku)"
              />
              <u-image
                width="160rpx"
                height="160rpx"
                radius="12rpx"
                :src="sku.image"
                margin="0 20rpx"
              />
              <view class="sku-info">
                <u-text :text="sku.name" size="28rpx" bold lines="2"></u-text>
                <u-text
                  :text="sku.spec"
                  size="24rpx"
                  color="#999"
                  margin="6rpx 0"
                ></u-text>
                <view class="sku-ctrl">
                  <u-text
                    :text="'¥'+sku.price"
                    color="var(--primary-color)"
                    size="32rpx"
                    bold
                  />
                  <u-number-box
                    v-model="sku.count"
                    :min="1"
                    :max="sku.stock"
                    integer
                    @change="changeCount(sku)"
                  />
                </view>
              </view>
            </view>
          </u-swipe-action-item>
        </u-swipe-action>

        <!-- 失效商品 -->
        <view v-if="store.invalidList.length" class="invalid-area">
          <u-divider text="以下商品已失效" textSize="24rpx" lineColor="#eee"/>
          <view
            class="invalid-item"
            v-for="item in store.invalidList"
            :key="item.cartId"
          >
            <u-image
              width="120rpx"
              height="120rpx"
              :src="item.image"
              radius="8rpx"
              marginRight="20rpx"
            />
            <view class="invalid-info">
              <u-text :text="item.name" size="28rpx" lines="2" color="#ccc"/>
              <u-text text="商品已下架或无货" size="24rpx" color="#ccc"/>
            </view>
            <u-button
              text="删除"
              size="mini"
              plain
              @click="deleteInvalid(item)"
            />
          </view>
        </view>
      </view>
    </view>

    <!-- 底部结算栏 -->
    <view v-if="!isEmpty" class="submit-bar">
      <view class="left">
        <u-checkbox
          :value="allChecked"
          shape="circle"
          label="全选"
          @change="toggleAll"
        />
      </view>
      <view class="center">
        <u-text text="合计：" size="28rpx"/>
        <u-text
          :text="'¥'+checkedAmount"
          color="var(--primary-color)"
          size="36rpx"
          bold
        />
        <u-text
          :text="'（已选'+checkedCount+'件）'"
          color="#999"
          size="24rpx"
          marginLeft="12rpx"
        />
      </view>
      <view class="right">
        <u-button
          :text="shopType==='food'?'去结算':'去结算'"
          type="primary"
          :color="shopType==='food'
            ? 'linear-gradient(90deg,#ff7d00 0%,#ff5700 100%)'
            : 'linear-gradient(90deg,#ff4b7e 0%,#ff3366 100%)'"
          :disabled="checkedCount===0"
          @click="toSettle"
        />
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      shopType: 'food', // food | mall
      cartGroup: [],      // 购物车分组
      swipeOptions: [{text: '删除', style: { backgroundColor: '#ff4d4f' }}]
    };
  },
  computed: {
    // 是否空车
    isEmpty() {
      return !this.cartGroup.length;
    },
    // 全选状态
    allChecked() {
      return this.cartGroup.every(s => s.checked) && this.cartGroup.length;
    },
    // 已选商品
    checkedItems() {
      return this.cartGroup
        .flatMap(s => s.list)
        .filter(i => i.checked);
    },
    checkedCount() {
      return this.checkedItems.reduce((t, i) => t + i.count, 0);
    },
    checkedAmount() {
      return this.checkedItems
        .reduce((t, i) => t + i.price * i.count, 0)
        .toFixed(2);
    }
  },
  onShow() {
    this.loadCart();
  },
  methods: {
    /* 模拟拉取购物车数据 */
    loadCart() {
      // 实际项目走接口
      this.cartGroup = [
        {
          storeId: 1,
          storeName: '麻辣香锅旗舰店',
          storeLogo: '/static/logo.png',
          checked: false,
          list: [
            {
              cartId: 101,
              skuId: 11,
              name: '招牌麻辣香锅',
              spec: '中辣 · 加香菜',
              image: '/static/banner.png',
              price: 68,
              count: 1,
              stock: 99,
              checked: false
            },
            {
              cartId: 102,
              skuId: 12,
              name: '冰柠檬水',
              spec: '常温 · 少冰',
              image: '/static/1.jpg',
              price: 8,
              count: 2,
              stock: 200,
              checked: false
            }
          ],
          invalidList: [
            {
              cartId: 103,
              name: '已下架小食',
              image: '/static/shop_headbg.png'
            }
          ]
        },
        {
          storeId: 2,
          storeName: '零食专卖店',
          storeLogo: '/static/R-C.png',
          checked: false,
          list: [
            {
              cartId: 201,
              skuId: 21,
              name: '每日坚果',
              spec: '30包/箱',
              image: '/static/swiper-background-1.png',
              price: 99,
              count: 1,
              stock: 5,
              checked: false
            }
          ],
          invalidList: []
        }
      ];
    },

    /* 店铺勾选 */
    toggleStore(store) {
      store.checked = !store.checked;
      store.list.forEach(i => i.checked = store.checked);
    },

    /* 商品勾选 */
    toggleSku(sku) {
      sku.checked = !sku.checked;
    },

    /* 全选/反选 */
    toggleAll() {
      const next = !this.allChecked;
      this.cartGroup.forEach(s => {
        s.checked = next;
        s.list.forEach(i => i.checked = next);
      });
    },

    /* 修改数量 */
    changeCount(sku) {
      // 实际项目：调用接口更新
      console.log('数量变化', sku);
    },

    /* 删除商品 */
    deleteItem(sku) {
      uni.showModal({
        title: '提示',
        content: '确定删除该商品？',
        success: res => {
          if (res.confirm) {
            // 实际项目：调用接口
            const store = this.cartGroup.find(s =>
              s.list.some(i => i.cartId === sku.cartId)
            );
            store.list = store.list.filter(i => i.cartId !== sku.cartId);
            if (!store.list.length && !store.invalidList.length) {
              this.cartGroup = this.cartGroup.filter(s => s.storeId !== store.storeId);
            }
          }
        }
      });
    },

    /* 删除失效商品 */
    deleteInvalid(item) {
      const store = this.cartGroup.find(s =>
        s.invalidList.some(i => i.cartId === item.cartId)
      );
      store.invalidList = store.invalidList.filter(i => i.cartId !== item.cartId);
      if (!store.list.length && !store.invalidList.length) {
        this.cartGroup = this.cartGroup.filter(s => s.storeId !== store.storeId);
      }
    },

    /* 去结算 */
    toSettle() {
      if (!this.checkedCount) return;
      uni.navigateTo({
        url: `/pages/settle/settle?ids=${this.checkedItems.map(i => i.cartId).join(',')}`
      });
    },

    /* 空车回首页 */
    goHome() {
      uni.switchTab({ url: '/pages/index/index' });
    }
  }
};
</script>

